body {
  min-height: 100vh;
}

.app {
  background-color: #060b26;
  max-width: 650px;
  min-width: 600px;
  height: -webkit-min-content;
  height: min-content;
  min-height: 250px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  
  justify-content: space-evenly;
 
  
}

.perfil {
  background-color: hsl(227, 33%, 22%);
  max-width: 450px;
  min-width: 400px;
  height: -webkit-min-content;
  height: min-content;
  min-height: 150px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
 
  
}


/* PREGUNTAS */
.lado-izquierdo {
  max-width: 400px;
  min-width: 350px;
  
  padding-right: 20px;

  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.pregunta-numero {
  margin-bottom: 20px;
}

.pregunta-numero span {
  font-size: 28px;
}

.pregunta-titulo {
  margin-bottom: 12px;
}

.lado-derecho {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color:white
}

.diagnostico {
  margin-top: 30px;
  background-color: hsl(227, 33%, 22%);
  max-width: 350px;
  min-width: 250px;
  height: -webkit-min-content;
  height: min-content;
  min-height: 100px;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}

/* FINALIZADO */
.juego-terminado {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 24px;
  align-items: center;
}

/* GRAL */
button {
  
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

.tiempo-restante {
  font-size: 14px;
  color: #fcbf3d;
  margin-bottom: 20px;
}



.btns-container {
  display: flex;
  justify-content: space-evenly;
}

button:hover {
  background-color: #234668;
}

button[disabled] {
  background-color: #555e7d;
  color: #ffffff;
  cursor: not-allowed;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}

.title-diagnostico h1{
  margin-bottom: 15px;
  padding-bottom: 5px;
  color: #353c4e;
  text-align: center;
}






