.navbar {
  position: relative;
  background-color: #060b26;
  height: 70px;


  display: flex;
  justify-content:space-between  ;
  align-items: center;
  flex-wrap: wrap;
  z-index: 9;  
}

.navbar-center {
  width: 68%;
  display: flex;
  justify-content:right  ;
  align-items: center;
  z-index: 9;
  
}
.navbar-rigth {
  width: 32%;
  display: flex;
  justify-content:right   ;
  align-items: center;

  z-index: 9;
  
}

.menu-bars {
  z-index: 9;
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
  cursor: pointer;
}

.nav-menu {
  z-index: 9;
  background-color: #060b26;
  width: 300px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 0;
 
  left: -100%;
  transition: 850ms;
}

.usernav {
  
  background-color: #060b26;
  width: 100px;
  height: 5vh;

  position: relative;

  transition: 850ms;
}


.nav-menu.active {
  left: 0;
  transition: 350ms;

}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #1a83ff;
}

.nav-menu-items {
  width: 100%;
}
.nav-item-user {
  width: 100%;
  
  text-align: center;
}

.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}


 .nav-dropdown.active{ 
   top: -20%;
   height: 0;
   transition: 350ms;
}

.nav-dropdown{
  
  z-index: 9;
  background-color: #060b26;
  width: 200px;
  height: 150px;
  display: flex;
  justify-content: start;
  position: fixed;
  top: 70;
  right: 0;
  transition: 350ms;

}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}


.navdropdown{
  
    float: left;
    overflow: hidden;

  

}


