body {
    text-align: center;
    font-family: 'Roboto', sans-serif;
  }
  
  .button_style {
    margin-left: 10px !important;
    margin-right: 10px;
  }
  
  .centerTable {
    margin: 0px auto;
  }
  
  h2 {
      color: #3f51b5;
  }
  
  
  .login{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  
  }
  
  
  .contenedor{
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 5% 0;
    
    
    
  
  }
  
  .imagen{
  
    width: 600px;
    height: 367px;
    background-size:contain;
    background-repeat: no-repeat;
  }
  
  .bloqueo{
    background: rgb(168, 165, 165) ;
    width: 100%;
    height: 000%;
    z-index:10;
    
  }
  
  
  @media (max-width:400px){
    .contenedor{
    
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 40% 0;
      
      
      
    
    }
  }
  
  @media (min-width:400px){
    .contenedor{
    
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 25% 0;
      
      
      
    
    }
  }
  
  @media (min-width:800px){
    .contenedor{
     
     display: flex;
     flex-direction: row;
     align-items: center;
     justify-content: center;
     padding: 15% 0;
     
   
   }
   }
  @media (min-width:1000px){
   .contenedor{
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 10% 0;
    
  
  }
  }
  